<template>
  <BaseLayout>
    <Breadcrumbs />
    <section class="brands-section">
      <BContainer>
        <BRow>
          <BCol lg="3" class="pb-30 pb-lg-25">
            <BrandsSidebarLinks :sidebarData="brands" />
          </BCol>
          <BCol lg="9">
            <span class="d-none d-lg-block page-title">{{ $t("brands") }}</span>
            <BRow>
              <BCol
                cols="6"
                md="4"
                v-for="(brandData, index) in brands"
                :key="`bp` + index"
              >
                <BLink
                  v-if="brandData.category != null"
                  :to="`/` + brandData.category.url_path"
                  class="brand-card"
                >
                  <div class="brand-logo">
                    <img
                      :src="brandData.logo"
                      :alt="brandData.title"
                      v-if="brandData.logo"
                    />
                  </div>
                  <span class="brand-name">{{ brandData.title }}</span>
                </BLink>
                <div class="brand-card" v-if="brandData.category == null">
                  <div class="brand-logo">
                    <img
                      :src="brandData.logo"
                      :alt="brandData.title"
                      v-if="brandData.logo"
                    />
                  </div>
                  <span class="brand-name">{{ brandData.title }}</span>
                </div>
              </BCol>
            </BRow>
            <div
              class="bottom-txt d-lg-block"
              v-html="getCmsData?.content"
            ></div>
          </BCol>
        </BRow>
      </BContainer>
    </section>
  </BaseLayout>
</template>

<script setup>
import { computed, onMounted } from "vue";
import { useStore } from "vuex";
import { useI18n } from "vue-i18n";
import BaseLayout from "@/base/core/components/common/BaseLayout";
import Breadcrumbs from "@/base/core/components/Breadcrumbs";
import BrandsSidebarLinks from "@/base/core/components/BrandsSidebarLinks";

const store = useStore();
const { t } = useI18n();

onMounted(() => {
  const bcrumb = { current: t("brands"), routes: [] };
  store.commit("breadcrumbs/set", bcrumb);
});

const brands = computed(() => store.getters["product/getBrandSlider"]);
const getCmsData = computed(() =>
  store.getters["cmsBlock/getCmsBlockByIdentifier"]("merken")
);
</script>
