<template>
  <div class="sidebar-holder">
    <BLink class="brands-opener d-lg-none" @click.prevent="toggleBrands"
      >Merken<span class="plus">+</span></BLink
    >
    <div
      class="brands-overlay"
      @click.prevent="toggleBrands"
      v-bind:class="{ show: brandsExpanded }"
    ></div>
    <div class="sidebar" v-bind:class="{ show: brandsExpanded }">
      <div class="d-lg-none brands-top">
        <span class="brands-collapse-heading">{{ $t("our_brands") }}</span>
        <i class="lnr lnr-cross" @click.prevent="toggleBrands"></i>
      </div>
      <span class="sidebar-heading"></span>
      <ul class="sidebar-links">
        <li v-for="(sidebarLink, index) in sidebarData" :key="'sidebarLink' + index">
          <BLink :to="`/` + sidebarLink.category.url_path" v-if="sidebarLink.category != null">{{
            sidebarLink.title
          }}</BLink>
        </li>
      </ul>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from "vue";

defineProps({
  sidebarData: Array,
});

const brandsExpanded = ref(false);

const toggleBrands = () => {
  brandsExpanded.value = !brandsExpanded.value;
};

const resize = () => {
  if (window.innerWidth > 991) {
    brandsExpanded.value = false;
  }
};

onMounted(() => {
  window.addEventListener("resize", resize);
});

onBeforeUnmount(() => {
  window.removeEventListener("resize", resize);
});
</script>
